import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { OfferService } from '../core/offer.service';
import { TableState } from '../core/table-state';
import { AuthUserService } from '../core/auth-user.service';

@Injectable()
export class OfferManagerService {
  public state: TableState = {
    page: 1,
    pageSize: 10,
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  }

  private _total$ = new BehaviorSubject<number>(1);
  get total$(): Observable<number> {
    return this._total$.asObservable();
  }

  private originalList = [];

  private _offerList$ = new BehaviorSubject<any[]>(null);
  get offerList$(): Observable<any[]> {
    return this._offerList$.asObservable();
  }

  // private _vendorList$ = new BehaviorSubject<any[]>(null);
  // get vendorList$(): Observable<any[]> {
  //   return this._vendorList$.asObservable();
  // }



  constructor(private offerService: OfferService, private authUserService: AuthUserService) { }

  addOffer(offer: any): Observable<any> {
    // const offer = {
    //   name: offer['name'],
    //   email: offer['email'],
    //   password: offer['password'],
    //   address: offer['address'],
    //   city: offer['city'],
    //   mobile: offer['mobile'],
    //   status: offer['status']
    // };
    return this.offerService.addOffer(offer);
  }

  updateOffer(offer: any): Observable<any> {
    // const offer = {
    //   _id: offer['_id'],
    //   name: offer['name'],
    //   email: offer['email'],
    //   address: offer['address'],
    //   city: offer['city'],
    //   mobile: offer['mobile'],
    //   status: offer['status']
    // };
    return this.offerService.updateOffer(offer);
  }

  deleteOffer(id: string): Observable<any> {
    return this.offerService.deleteOffer(id);
  }

  getOffer(id: string): Observable<any> {
    return this.offerService.getOffer(id);
  }


  getOfferList(): void {
    this.offerService.getOfferList(this.authUserService.user._id)
      .subscribe((data: any[]) => {
        console.log('Got Offers for you: ', data);
        this._offerList$.next(data);
        this._total$.next(data.length);
        this.originalList = data;
      });
  }

  // getVendorList(): void {
  //   this.vendorService.getVendorList()
  //     .subscribe((data: any[]) => {
  //       console.log('Got Vendors for you: ', data);

  //       this._vendorList$.next(data);
  //     });
  // }

  searchText(): void {
    if (!this.state.searchTerm) {
      this._offerList$.next(this.originalList);
      this._total$.next(this.originalList.length)
    } else {
      const searchTerm = this.state.searchTerm.toLocaleLowerCase();
      const filteredList = this.originalList.filter((item: any) => item['name'] && item['name'].toLocaleLowerCase().includes(searchTerm) ||
        item['status'] && item['status'].toLocaleLowerCase().includes(searchTerm) ||
        item['off'] && item['off'].toString().toLocaleLowerCase().includes(searchTerm) ||
        item['off_type'] && item['off_type'].toLocaleLowerCase().includes(searchTerm) ||
        item['coupon_code'] && item['coupon_code'].toLocaleLowerCase().includes(searchTerm));
      this._offerList$.next(filteredList);
      this._total$.next(filteredList.length);
    }
  }
}