import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { Subject } from 'rxjs';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { OfferManagerService } from '../offer-manager.service';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-add-offer',
  templateUrl: './add-offer.component.html'
})
export class AddOrUpdateOfferComponent implements OnInit , OnDestroy {
  private ngUnsubcribe = new Subject();
  offerForm: FormGroup;
  processing = false;
  error: string;

  @Input()
  offer: any;

  @Input()
  operation: 'CREATE' | 'UPDATE' = 'CREATE';
  constructor(
    // private router: Router,
     private formBuilder: FormBuilder,
     private activeModal: NgbActiveModal,
     public offerManager: OfferManagerService,
    // private authManagerService: AuthManagerService,
    // private route: ActivatedRoute,

  ) {
  }
  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }

  ngOnInit(): void {

    // this.offerManager.getVendorList();
    this.offerForm = this.formBuilder.group({
      offer_id : this.offer.offer_id,
      name: [this.offer.name, [Validators.required]],
      owner_id: [this.offer.owner_id,[Validators.required]],
      off: [this.offer.off,[Validators.required]],
      off_type: [this.offer.off_type,[Validators.required]],
      coupon_code: [this.offer.coupon_code, [Validators.required]],
      status: [this.offer.status, Validators.required],
      min_value: [this.offer.min_value, Validators.required],
      max_value: [this.offer.max_value, Validators.required],
      validity: [this.offer.validity],

    });
    this.offerForm.valueChanges
    .pipe(takeUntil(this.ngUnsubcribe))
    .subscribe((data: any) => {
      console.log('data: ', data);
      
    })
  }

  submit(): void {
    const offer = this.offerForm.value;
    offer.vendor = typeof offer.vendor === 'string' ? JSON.parse(offer.vendor) : offer.vendor;
    if (this.operation == 'CREATE') {
      offer.created_date = new Date().valueOf();
      this.offerManager.addOffer(offer)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe({
          next: (data: any) => {
          this.activeModal.close(true);
          console.log('data: ', data.vendor);
        },
        error:(error: any)=> {
          this.error = error['error']['error'];
          this.processing = false;
        }});
    } else {
      offer.updated_date = new Date().valueOf();
      this.offerManager.updateOffer(offer)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe({
          next: (data: any) => {
          this.activeModal.close(true);
          console.log('data: ', data.vendor);
        },
        error:(error: any)=> {
          this.error = error['error']['error'];
          this.processing = false;
        }});
    }
  }
  cancel(): void {
    this.activeModal.close(false);
  }
}
