import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject, from } from 'rxjs';
import { OfferManagerService } from '../offer-manager.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { filter, takeUntil } from 'rxjs/operators';
import { AddOrUpdateOfferComponent } from '../add-offer/add-offer.component';
import { ConfirmDeleteComponent } from '../confirm-delete/confirm-delete.component';
import { AuthUserService } from 'src/app/core/auth-user.service';

@Component({
  selector: 'app-offer-list',
  templateUrl: './offer-list.component.html'
})
export class OfferListComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();

  constructor(public offerManagerService: OfferManagerService,
    private ModalService: NgbModal,
    private authUserService: AuthUserService) { }

  ngOnInit(): void {
    this.offerManagerService.getOfferList();
  }

  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }


  addOfferModal(): void {
    const modalRef = this.ModalService.open(AddOrUpdateOfferComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    const offer = {
      name: '',
      owner_id: this.authUserService.user._id,
      off: 0,
      off_type: 'Fixed',
      coupon_code: '',
      image: null,
      status: 'Active',
      min_value: 0,
      max_value:0,
      validity: ''
    }
    modalRef.componentInstance.offer = offer;
    modalRef.componentInstance.operation = 'CREATE';
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.offerManagerService.getOfferList());
  }

  deleteOffer(offer: any): void {
    const modalRef = this.ModalService.open(ConfirmDeleteComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    modalRef.componentInstance.offer = offer;
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.offerManagerService.getOfferList());
  }

  updateOffer(offer: any): void {
    const modalRef = this.ModalService.open(AddOrUpdateOfferComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    modalRef.componentInstance.offer = offer;
    modalRef.componentInstance.operation = 'UPDATE';
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.offerManagerService.getOfferList());
  }
}
