import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Subject } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { takeUntil } from 'rxjs/operators';
import { OfferManagerService } from '../offer-manager.service';

@Component({
  selector: 'app-confirm-delete',
  templateUrl: './confirm-delete.component.html'
})
export class ConfirmDeleteComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();

  @Input()
  offer: any;

  constructor(private activatedRoute: ActivatedRoute, private activeModal: NgbActiveModal, private offerManagerService: OfferManagerService) { }

  ngOnInit(): void {

  }

  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }

  delete(): void {
    this.offerManagerService.deleteOffer(this.offer.offer_id)
      .pipe(takeUntil(this.ngUnsubcribe))
      .subscribe((data: any) => {
        this.activeModal.close(true);
        console.log('data: ', data.offer);
      });
  }

  cancel(): void {
    this.activeModal.close(false);
  }
}
